import React from 'react';
import logo from './logo.svg';
import classes from './App.module.css'
import { useState } from 'react';
import { v1 } from 'uuid';
import { Todolist } from './Todolist';
import { Input } from './Input';
import { useDispatch } from 'react-redux';
import { useSelect } from '@mui/base';
import { useSelector } from 'react-redux';
import { AppRootState } from './state/Store';
import { useCallback } from 'react';

import {  RemoveTaskAC } from './state/TasksReducer';
import { AddTaskAC } from './state/TasksReducer';
import { ChangeTaskisDoneAC } from './state/TasksReducer';
import { ChangeTaskTitleAC } from './state/TasksReducer';
import { ChangeTodolistFilterAC } from './state/TodolistReducer';
import { ChangeTodolistTitleAC } from './state/TodolistReducer';
import { DeleteTodolistAC } from './state/TodolistReducer';
import { AddTodolistTaskAC } from './state/TodolistReducer';
import { ACreater } from './state/TodolistReducer';
import { useEffect } from 'react';



export interface TaskType {
  id: string
  title: string
  isDone: boolean
}

 export type TaskStateType = {
  [key: string]: TaskType[]
}

export interface TodolistType {
  id: string
  title: string
  filter: Filter
  order: number
}

export type Filter = 'all' | 'activ' | 'completed'

function App() {

let dispatch = useDispatch()
let todolist = useSelector<AppRootState, Array<TodolistType>>(state => state.todolists)
let state = useSelector<AppRootState, TaskStateType>(state => state.tasks)

let orderStart: any
let idStart: any

const dragStart = useCallback((event: any, item: any) => {
 orderStart= item.order
  idStart=item.id  
  
 }, [orderStart, idStart])
 
 const dragOver = useCallback((event: any) => {
     event.preventDefault()
     event.currentTarget.style.background='beige'
 },[])

 const dragLeav =useCallback((event: any) => {
  event.currentTarget.style.background='white'
 },[])
 
 const dragDrop = useCallback((event: any, item: any) => {
     event.preventDefault()
     event.currentTarget.style.background='white'
     let action = ACreater(item, idStart, orderStart)
    dispatch(action)
 },[dispatch])
 
 const ChangeTodolistFilter = useCallback((todolistId: string, fil:Filter) => {
  let action = ChangeTodolistFilterAC(todolistId, fil)
  dispatch(action)
},[dispatch])

const ChangeTodolistTitle =useCallback((todolistId: string, title: string) => {
  let action = ChangeTodolistTitleAC(todolistId, title)
  dispatch(action)
  },[dispatch])

const deleteTodolist =useCallback((todolistId: string) => {
  let action = DeleteTodolistAC(todolistId)
  dispatch( action)
},[dispatch])

const ChangeTaskTitle = useCallback((todiltsId:string, taskId: string, title: string) => {
  let action = ChangeTaskTitleAC(todiltsId, taskId, title )
  dispatch(action)
  },[dispatch])

const addTodolist =useCallback((state: string) => {
  let action = AddTodolistTaskAC(state)
  dispatch(action)
},[])


const ChangeTaskisDone = useCallback((todolistId: string, taskId:string, isDone: boolean) => {
  let action = ChangeTaskisDoneAC(todolistId, taskId, isDone)
  dispatch(action)

 },[dispatch])

const DeleteTask =useCallback((todolistId: string, taskId: string) => {
  let action = RemoveTaskAC(todolistId, taskId)
  dispatch(action)
  },[dispatch])

const addTask =useCallback((todolistId: string, states: string)=> {
  let action = AddTaskAC(todolistId, states)
  dispatch(action)
},[dispatch])




const sortCard = useCallback((a: any, b: any) => {
   if (a.order > b.order) {
    return 1
  } else {
    return -1
  }
},[])


  return (
    <div className={classes.fon}>
      <div className={classes.newTod}>
       <Input AddFunc={addTodolist}/>
      </div>

      <div className={classes.container}>

      {
      todolist.sort(sortCard).map(item => {
        let states = state[item.id]
          if (item.filter === 'completed') {
          states = states.filter(item => item.isDone === true)
        }
        if (item.filter === 'activ') {
          states = states.filter(item => item.isDone === false)
        }
       
          return (
            <Todolist key={item.id}
                        states={states}
                        title={item.title}
                        ChangeTodolistFilter={ChangeTodolistFilter}
                        todolistId={item.id}
                        ChangeTodolistTitle={ChangeTodolistTitle}
                        ChangeTaskisDone={ChangeTaskisDone}
                        DeleteTask={DeleteTask}
                        addTask={addTask}
                        ChangeTaskTitle={ChangeTaskTitle}
                        deletetodolist={deleteTodolist}
                        item={item}
                        dragStart={dragStart}
                        dragOver={dragOver}
                        dragDrop={dragDrop}
                        dragLeav={dragLeav}
                        filter={item.filter}
                        />
              )
         
        
      })
    }
      </div>
     
    

    </div>
  );
}

export default App;
