import { v1 } from "uuid"
import { TodolistType } from "../App"

export let todolist1 = v1()
export let todolist2 = v1()




type ChangeTodolistFilterType = {
  type: 'CHANGE-TODOLIST-FILTER',
  todolistId: string,
  filter: 'all' | 'activ' | 'completed'
}

type ChangeTodolistTitleType = {
  type: 'CHANGE-TODOLIST-TITLE',
  todolistId: string,
  title: string
}

type DeleteTodolistType = {
  type: 'DELETE-TODOLIST',
  todolistId: string
}

type AddTodolistTaskType = {
  type: 'ADD-TODOLIST-TASK',
  v: string,
  state: string,
  result: number
}

type AC = {
  type: 'AC',
  item: any
  idStart: any
  orderStart: any
}

type filter = 'all' | 'activ' | 'completed'



type ActionsType = ChangeTodolistFilterType | ChangeTodolistTitleType | DeleteTodolistType | AddTodolistTaskType | AC




let initialState: TodolistType[] = [
    {id: todolist1, title:'Почему Вам стоить меня взять к себе на работу..' , filter: 'all', order: 1},
    {id:todolist2, title: 'Я обладаю начальными знаниями, но все профессионалы однажды были такими как я', filter: 'all', order: 2},
        
  ]

  let result =2
function func() {
  result++
  
}



export const TodolistReducer= (state: TodolistType[] = initialState, action: ActionsType): TodolistType[] => {

  switch (action.type) {
    case 'CHANGE-TODOLIST-FILTER': {
      let copy = [...state]
      let res = copy.map(item => item.id === action.todolistId ? {...item, filter: action.filter}: item)
      return res
    }
    case 'CHANGE-TODOLIST-TITLE' :{
      let copy = [...state]
      let res = copy.map(item => item.id === action.todolistId ? {...item, title: action.title}: item)
      return res
    }
    case 'DELETE-TODOLIST': {
      let copy = [...state]
      let res = copy.filter(item => item.id !== action.todolistId)
      return res
    }
    case 'ADD-TODOLIST-TASK': {
      func()
      let copy = [...state]
      let res: TodolistType = {id: action.v,title: action.state, filter: 'all', order: ++action.result}
      let result = [...copy, res]
      return result
    }
    case 'AC': {
           let copy = [...state]
      let result = copy.map(items => {
        if (items.id === action.item.id){
          return {...items, order: action.orderStart}
        }
        if (items.id === action.idStart) {
          return {...items, order: action.item.order}
        }
        return items
      })
      
      return result
    }
   
   
  }
   
    return state
}



export const ChangeTodolistFilterAC = (todolistId: string, filter: filter): ChangeTodolistFilterType => {
  return {type: 'CHANGE-TODOLIST-FILTER', todolistId, filter}
}

export const ChangeTodolistTitleAC = (todolistId: string, title: string): ChangeTodolistTitleType => {
  return {type: 'CHANGE-TODOLIST-TITLE', todolistId,title}
}

export const DeleteTodolistAC = (todolistId: string): DeleteTodolistType => {
  return {type: 'DELETE-TODOLIST', todolistId}
}

export const AddTodolistTaskAC = (state: string): AddTodolistTaskType => {
  let v = v1()
  
  return {type: 'ADD-TODOLIST-TASK', v, state, result}
}

export const ACreater = (item: any, idStart: any, orderStart: any): any => {
  return { type :'AC', item, idStart, orderStart}
}