import React from 'react'
import { useState } from 'react'
import classes from './App.module.css'

import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Icon from '@mui/material/Icon';


type Props = {
    AddFunc: (state: string) => void
}




export const Input =(props: Props) => {
   const [state, setState] = useState('');
   const [error, setError] = useState(false)


   const changeInput =(event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setError(false)
    setState(event.target.value)
   }

   

    const add =() => {
        setState('')

        if (state.length === 0) {
            setError(true)
        } else {
            {props.AddFunc(state)}
        }
    }

   const onKeyPress = (event: React.KeyboardEvent<HTMLDivElement>) => {
    if (event.keyCode === 13 && state.length !== 0) {
       {props.AddFunc(state)}
       setState('')
    } else if (event.keyCode === 13 && state.length === 0) {
        setError(true)
    }
   }


    return (
        <div>
             <TextField   value={state}  
                           onKeyDown={event => {onKeyPress(event)}} 
                             onChange={event => {changeInput(event)}} 
                                label={state} color="primary" focused  
                                  className={error? classes.error: ''}  
                                     size='small'/>

          
           
              <Button   color="secondary" onClick={() => {add()}}> Add</Button>
         

              {error? <div className={classes.error}> Error </div>: ''}
        </div>
       
    )
}