import { todolist1 } from "./TodolistReducer"
import { todolist2 } from "./TodolistReducer"
import { v1 } from "uuid"
import { TaskStateType } from "../App"
import { TaskType } from "../App"

import { AddTodolistTaskAC } from "./TodolistReducer"



const initialState: TaskStateType = {
    [todolist1]: [
      {id: v1(), title: 'Хочу развиваться', isDone: false},
      {id:v1(), title: 'Люблю труд ', isDone: false},
      {id:v1(), title: 'Честен', isDone: false},
      {id:v1(), title: 'Порядочен', isDone: false},
      
    ],
    [todolist2]: [
      {id:v1(), title: 'Я достигну цели', isDone: false},
      {id:v1(), title: 'Всему научусь', isDone: false},
      {id:v1(), title: 'Все будет хорошо', isDone: false}
    ]
  }

type AddTaskType = {
    type: 'ADD-TASK',
    todolistId: string,
    title: string
}

type deleTaskType = {
    type: 'REMOVE-TASK',
    todolistId: string,
    taskId: string
}

type ChangeTaskisDoneType = {
    type: 'CHANGE-TASK-ISDONE',
    todolistId: string,
    taskId: string,
    isDone: boolean
}


type ChangeTaskTitleType = {
    type: 'CHANGE-TASK-TITLE',
    todolistId: string,
    taskId: string,
    title: string
}

type AddTodolistTaskAC = {
    type: 'ADD-TODOLIST-TASK',
    v: string
    
}

type ActionsType =  AddTaskType | deleTaskType | ChangeTaskisDoneType | ChangeTaskTitleType | AddTodolistTaskAC


export const TasksReducer =(state: TaskStateType= initialState, action: ActionsType): TaskStateType => {
    switch(action.type) {
        case 'ADD-TASK': {
            let copy = {...state}
            let res: TaskType ={id: v1(),title: action.title, isDone: false}
            let result = copy[action.todolistId]
            let arr = [...result, res]
            copy[action.todolistId] = arr
            return copy
        }
        case 'REMOVE-TASK' : {
           let copy = {...state}
           let result = copy[action.todolistId].filter(item => item.id !== action.taskId)
           copy[action.todolistId] = result
           return copy
        }
        case 'CHANGE-TASK-ISDONE' : {
            let copy = {...state}
            let res = copy[action.todolistId].map(item => item.id === action.taskId ? {...item, isDone: !action.isDone}: item)
            copy[action.todolistId] = res
            return copy
        }
        case 'CHANGE-TASK-TITLE': {
            let copy = {...state}
             let res = copy[action.todolistId].map(item => item.id === action.taskId? {...item, title: action.title}: item)
             copy[action.todolistId] = res
              return copy
        }
        case 'ADD-TODOLIST-TASK': {
            let copy = {...state}
            let res: TaskType[]= []
            copy[action.v] = res
            return copy
        }
    }
    
    
    return state
}




export const AddTaskAC =(todolistId: string, title: string): AddTaskType => {
    return {type: 'ADD-TASK', todolistId, title}
}

export const RemoveTaskAC = (todolistId: string, taskId: string): deleTaskType => {
    return {type: 'REMOVE-TASK', todolistId,taskId}
}

export const ChangeTaskisDoneAC = (todolistId: string, taskId: string, isDone: boolean): ChangeTaskisDoneType => {
    return {type: 'CHANGE-TASK-ISDONE', todolistId,taskId,isDone}
}

export const ChangeTaskTitleAC = (todolistId: string, taskId: string, title: string): ChangeTaskTitleType => {
    return { type: 'CHANGE-TASK-TITLE', todolistId,taskId,title}
}