import React from 'react'
import { TaskType } from './App'
import classes from './App.module.css'
import { Filter } from './App'
import { Input } from './Input'
import { useState } from 'react'
import { Task } from './Task'
import Button from '@mui/material/Button';



type PropsType = {
    deletetodolist: (todolistId: string) => void,
    key: string,
    states: TaskType[],
    title: string,
    ChangeTodolistFilter: (todolistId: string, fil: Filter) => void,
    ChangeTodolistTitle: (todolistId: string, title: string) => void,
    DeleteTask: (todolistId: string, taskId: string) => void,
    ChangeTaskisDone: (todiltId: string, taskId: string, isDone: boolean) => void,
    todolistId: string,
    addTask: (todolistId: string, state: string) => void,
    ChangeTaskTitle: (todolistId: string, taskId: string, title: string) => void,
    item: any
    dragStart: (event: any, item: any) => void
    dragOver: (event: any) => void
    dragDrop: (event: any, item: any) => void
    dragLeav: (event: any) => void
    filter: Filter
}

export const  Todolist =  React.memo((props: PropsType) => {
    console.log(props.states)
    let [click, setClick] = useState(false)
    let [mouse, setMouse] = useState(false)

const AddFunc = (state: string) => {
    {props.addTask(props.todolistId, state)}
}
    
    return (
        
        <div className={classes.todolist}
            draggable={true}
                onDragStart={event => {props.dragStart(event, props.item )}}
                onDragOver={event => {props.dragOver(event)}}
                onDrop ={event => {props.dragDrop(event, props.item)}}
                onDragLeave={event => {props.dragLeav(event)}}
                onMouseMove={event => {setMouse(true)}}
                onMouseLeave={event => {setMouse(false)}}
        >
               

            <div className={classes.del}>
            {mouse? <span className={classes.span}> Drag me :)</span>: <span></span>}
            <Button size='small' variant="outlined" onClick={() => {props.deletetodolist(props.todolistId)}}>X</Button>
               
            </div>
            <div className={classes.title} onDoubleClick={()=> setClick(true)}>
               {click? <input value={props.title} 
                                onChange={event => {props.ChangeTodolistTitle(props.todolistId, event.target.value)} }
                                    onBlur={() => {setClick(false)}}/>: <span > {props.title}</span>}
            </div>

            <div className={classes.input}>
                <Input AddFunc={AddFunc} />
                
            </div>

               <div>
       
               <ol>
                {
                    props.states.map(item => {
                        return (
                            <div>
                               <li> 
                               <Task key={item.id}
                                        title={item.title}
                                            DeleteTask={props.DeleteTask}
                                            taskId={item.id}
                                            todolistId={props.todolistId}
                                            ChangeTaskisDone={props.ChangeTaskisDone}
                                            isDone={item.isDone}
                                            ChangeTaskTitle={props.ChangeTaskTitle}/>
                                </li>
                            </div>
                        )
                            
                                
                            
                            
                            
                        
                    })
                }
               </ol>
               </div>
                   
            
                

                <div className={classes.buttoncont}>
                <Button  variant={props.filter === 'all'? 'contained': 'outlined'} color="secondary" onClick={() => {props.ChangeTodolistFilter(props.todolistId, 'all')}}>All</Button>
                <Button  variant={props.filter === 'activ'? 'contained': 'outlined'} color="secondary" onClick={() => {props.ChangeTodolistFilter(props.todolistId, 'activ')}}>ACTIVE</Button>
                <Button  variant={props.filter === 'completed'? 'contained': 'outlined'} color="secondary" onClick={() => {props.ChangeTodolistFilter(props.todolistId, 'completed')}}>COMPLETED</Button>
                   

                </div>

        </div>
    )
})