import React, { useState } from 'react';
import classes from './App.module.css'

import IconButton from '@mui/material/IconButton';
import DeleteIcon from '@mui/icons-material/Delete';

import Checkbox from '@mui/material/Checkbox';


type Props = {
    key: string,
    title: string
    DeleteTask: (todolistId:string, taskId: string)=> void
    taskId: string
    todolistId: string
    ChangeTaskisDone: (todolistId: string, taskId: string, isDone: boolean ) => void
    isDone: boolean
    ChangeTaskTitle: (todolistId: string, taskId: string, title: string) => void


}




export const Task =(props: Props)=> {
   let [state, setState] = useState(false)
    let [value, setValue] = useState('')

    const Func = (event: string) => {
        setValue(event)
        {props.ChangeTaskTitle(props.todolistId, props.taskId, value)}
    }

    

    const Double =( ) => {
        setState(true)
        setValue(props.title)
    }

    return (
    <div>
        {state? <input value={value} onBlur={() => setState(false)} onChange={event => {Func(event.target.value)}} />: <span className={props.isDone? classes.completed: ''} onDoubleClick={()=> {Double()}}>{props.title}</span>}
        
        <Checkbox  checked={props.isDone} onChange={() => {props.ChangeTaskisDone(props.todolistId, props.taskId, props.isDone )}}/>
       



              <IconButton color="primary">
            <DeleteIcon onClick={() => {props.DeleteTask(props.todolistId, props.taskId)}}/>
        </IconButton>
    </div>
    )
}